export default {
    install(Vue){        
        Vue.prototype.$message = function(text){
            let toast = document.getElementById('Toast')            
            if(toast){
                toast.innerHTML = text
                toast.className = 'show message'
                setTimeout(() => {
                    toast.className = ''
                }, 3000)
            }            
        }
        Vue.prototype.$error = function(text){
            let toast = document.getElementById('Toast')
            if(toast){
                toast.innerHTML = '[Ошибка]: ' + text
                toast.className = 'show error'
                setTimeout(() => {
                    toast.className = ''
                }, 3000)
            }            
        }
        Vue.prototype.$success = function(text){
            let toast = document.getElementById('Toast')
            if(toast){
                toast.innerHTML = text
                toast.className = 'show success'
                setTimeout(() => {
                    toast.className = ''
                }, 3000)
            }            
        }
    }
}