export default{
    state: {
        ordersAmount: null,
        productsAmount: null,
        newOrdersAmount: null
    },
    getters: {
        ordersAmount(state){
            return state.ordersAmount
        },
        productsAmount(state){
            return state.productsAmount
        },
        newOrdersAmount(state){
            return state.newOrdersAmount
        }
    },
    mutations: {
        updateProducts(state, products){
            state.products = products
        }
    },
    actions: {
        fetchOrdersAmount({state}){
            state.ordersAmount = 1345
        },
        fetchProductsAmount({state}){
            state.productsAmount = 1000
        },
        fetchNewOrdersAmount({state}){
            state.newOrdersAmount = 10
        }
    }
}