<template>
    <header class="header">
        <form class="live-search">
            <input class="live-search__input" type="text" placeholder="Поиск...">

            <button class="live-search__btn" type="submit">
                <i class="live-search__icon zmdi zmdi-search"></i>
            </button>
        </form>

        <div class="user-section">
            <div class="account js-menu-toggle" :class="{show_dropdown: isOpen}">
                <div class="user-pic">
                    <img class="user-pic__img" :src="userpic" alt="">
                </div>

                <div class="user-menu" @click="isOpen = !isOpen">
                    <button class="user-menu__btn" type="button">
                       {{username}}
                    </button>
                </div>

                <div class="account-dropbox js-dropdown">
                    <div class="account-dropbox-header clearfix">
                        <div class="account-dropbox-pic">
                            <img class="account-dropbox-pic__img" :src="userpic" alt="">
                        </div>

                        <div class="account-dropbox-info">
                            <div class="account-dropbox-info__name">
                                {{username}}
                            </div>

                            <div class="account-dropbox-info__role">
                                administrator
                            </div>
                        </div>
                    </div>

                    <div class="account-dropbox-footer">
                        <a class="account-dropbox-item" href="#" @click.prevent="logout">
                            <i class="account-dropbox-item__icon zmdi zmdi-power"></i>
                            Logout
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </header>
</template>

<script>
export default {
    data: () => ({
        isOpen: false,
        notiIsOpen: false,  
        userpic: require('@/assets/img/userpic.png')      
    }),
    computed: {
        username(){
            return this.$store.getters.userData.name
        }
    },
    methods: {
        async logout(){
            await this.$store.dispatch('logout')
            this.$router.push('/?message=logout')
        }
    }
}
</script>