import Vue from 'vue'
import App from './App.vue'
import Vuelidate from 'vuelidate'
import VueMeta from 'vue-meta'
import './registerServiceWorker'
import router from './router'
import store from './store'
import Loader from '@/components/app/Loader'
import Toast from '@/components/admin/widgets/Toast'
import MessagePlugin from '@/utils/message.plugin'
import TitlePlugin from '@/utils/title.plugin'
import Paginate from 'vuejs-paginate'
import './assets/js/script.js'

import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'

Vue.config.productionTip = false

Vue.use(Vuelidate)
Vue.use(MessagePlugin)
Vue.use(VueMeta)
Vue.use(TitlePlugin)
Vue.component('Loader', Loader)
Vue.component('Toast', Toast)
Vue.component('Paginate', Paginate)

firebase.initializeApp({
  apiKey: "AIzaSyCTcTfxvWSxO5SjKN84m_Jz11Tjoqq_fVY",
  authDomain: "vue-catalog-admin.firebaseapp.com",
  databaseURL: "https://vue-catalog-admin.firebaseio.com",
  projectId: "vue-catalog-admin",
  storageBucket: "vue-catalog-admin.appspot.com",
  messagingSenderId: "744873896747",
  appId: "1:744873896747:web:fc2217b5b639c04fbd736e"
});

let app

firebase.auth().onAuthStateChanged(() => {
  if(!app){
    app = new Vue({
      router,
      store,
      render: h => h(App)
    }).$mount('#app')
  }
})