<template>
  <div id="app">

    <component :is="layout">
      <router-view/>
    </component>

  </div>
</template>

<script>
import AdminLayout from '@/layouts/AdminLayout'
import LoginLayout from '@/layouts/LoginLayout'

export default {
  computed: {
    layout(){
      return (this.$route.meta.layout || 'login') + '-layout'
    }
  },
  components:{
    AdminLayout, LoginLayout
  }
}
</script>