export default {
    state: {
        orders: {
        }
    },
    mutations: {
        setOrdersData(state, data){
            state.orders = data
        }
    },
    actions: {
        async fetchOrders({commit}){
            try{
                const orders = [
                    {
                        id: 1,
                        personName: 'Имя',
                        personSurname: 'Фамилия',
                        status: 0,
                        price: 3000,
                        created: this.getters.date
                    },
                    {
                        id: 2,
                        personName: 'Имя',
                        personSurname: 'Фамилия',
                        status: 1,
                        price: 4000,
                        created: this.getters.date
                    },
                    {
                        id: 3,
                        personName: 'Имя',
                        personSurname: 'Фамилия',
                        status: 2,
                        price: 6000,
                        created: this.getters.date
                    }
                ]
                commit('setOrdersData', orders)
            } catch(e){
                console.log(e.message)
            }
        }
    },
    getters: {
        orders: s => s.orders,
        date: () => {
            let options = {
                day: '2-digit',
                month: '2-digit',
                year: 'numeric',
                hour: '2-digit',
                minute: '2-digit',
                second: '2-digit'
            }
            return new Intl.DateTimeFormat('ru-RU', options).format(new Date)
        }
    }
}