<template>
    <div class="login-layout">
        <router-view/>
        <Toast/>
    </div>
</template>

<script>
import messages from '@/utils/messages'

export default {
    computed: {
        error(){
            return this.$store.getters.error
        }
    },
    watch: {
        error(fbError){
            this.$error(messages[fbError.code])           
        }
    }
}
</script>