export default{
    state: {
        error: null,
        success: null
    },
    mutations: {
        setError(state, error){
            state.error = error
        },
        clearError(state){
            state.error = null
            state.success = null
        },
        setSuccess(state, success){
            state.success = success
        }
    },  
    getters: {
        error: state => state.error,
        success: state => state.success
    }
}