<template>
    
    <li
        v-if="!item.children"
        @click="collapseMenu"
    >
        <router-link     
        class="menu__link"   
        active-class="active"
        :to="item.url"
        :exact="item.exact"
        >
            <i :class="iconClass"></i>
            
            {{item.title}}
        </router-link>

    </li>

    <li 
        v-else
    >
        <button
            class="menu__button"
            @click="menuToggle"
        >

            <i :class="iconClass"></i>

            {{item.title}}

            <i class="menu__shevron fa fa-angle-left"></i>
        </button>

        <ul class="menu__sublist">
            <li
                v-for="child in item.children"
                :key="child.url"
                active-class="active"
                :to="child.url"
                :exact="child.exact"
            >
                <router-link     
                class="menu__sublink"   
                active-class="active"
                :to="child.url"
                :exact="child.exact"
                >                    
                    {{child.title}}

                </router-link>
            <li/>
        </ul>
    </li>
</template>
<script>
export default {
    props: ['item'],
    data: () => ({
        buttons: document.getElementsByClassName('menu__button')
    }),
    computed: {
        iconClass(){
            return 'menu__icon ' + this.item.icon
        }
    },
    methods: {
        collapseMenu(){
            this.buttons.forEach( el =>{
                el.classList.remove('open')
            })
        },
        menuToggle(){
            if(this.$el.firstChild.className != 'menu__button open'){
                    this.collapseMenu()
                    this.$el.firstChild.classList.add('open')
                }else{
                    this.$el.firstChild.classList.remove('open')
                }
        }
    }
}
</script>