import firebase from 'firebase/app'

export default{
    state: {
        products: null,
        singleProduct: null
    },
    getters: {
        products(state){
            return state.products
        },
        singleProduct(state){
            return state.singleProduct
        }
    },
    mutations: {
        updateProducts(state, products){
            state.products = products
        },
        updateSingleProduct(state, product){
            state.singleProduct = product
        }
    },
    actions: {
        async fetchProducts({commit}){
            try {
                const products = (await firebase.database().ref('products').once('value')).val() || {}
                const prods = Object.keys(products).map(key => ({...products[key], key}))               
                commit('updateProducts', prods)
            } catch(e) {
                commit('setError', e)
                throw e.code
            }
        },
        async fetchSingleProduct({commit}, id){
            const response = (await firebase.database().ref('products').orderByChild('id').equalTo(id).once('value') || {}).val()
            const product = Object.keys(response).map(key => ({...response[key], key}))[0]
            commit('updateSingleProduct', product)
        },
        async createProduct({commit}, {id, title, price, status, sizes}){
            try {
                await firebase.database().ref('products').push({id, title, price, status, sizes})
            } catch (e) {
                commit('setError', e)
                throw e
            }
        },
        async updateProduct({commit}, {key, title, price, status, sizes}){
            try {
                await firebase.database().ref(`products`).child(key).update({title, price, status, sizes})
            } catch (e) {
                commit('setError', e)
                throw e
            }            
        },
        async deleteFromDB({commit, state}, id){
            if(confirm('Удалить товар из БД?')){
                try{
                    await firebase.database().ref(`products`).child(id).remove()
                    let products = state.products.filter(el => el.key !== id)
                    commit('updateProducts', products)
                    commit('setSuccess', 'Товар удален из БД')
                }catch (e) {
                    commit('setError', e)
                    throw e
                }  
            }
        },
        async changeStatus({commit, state}, {id, status}){
            try{
                await firebase.database().ref(`products`).child(id).update({status})
                let products = state.products.map(el => {
                    if(el.key == id){
                        el.status = !el.status
                        return el
                    }
                    return el
                })
                commit('updateProducts', products)
            }catch (e) {
                commit('setError', e)
                throw e
            } 
        }
    }
}